import React, { useEffect, useRef } from "react";

import './QuestionCount.css';

export default function ProgressBar(props) {
    const progressFill = useRef();
    useEffect(() => {
        progressFill.current.style.width = `${props.counter * 10}%`;
    });

    return (
        <div id="progressBar">
            <p>{props.counter}/12</p>
            <div id="progressContainer">
                <div id="progressFill"ref={progressFill}></div>
            </div>
        </div>
    )
}