import React, { Component } from 'react';
import quizQuestions from './api/quizQuestions';
import Quiz from './components/Quiz';
import Result from './components/Result';
// import logo from './svg/logo.svg';
import './index.css';
import esfp from './assets/11.svg';
import entp from './assets/5.svg';
import istp from './assets/12.svg';
import istj from './assets/1.svg';
import isfp from './assets/2.svg'
import entj from './assets/3.svg'
import esfj from './assets/4.svg'
import infp from './assets/7.svg'
import enfp from './assets/8.svg'
import estp from './assets/9.svg'
import intj from './assets/1.svg'
import intp from './assets/intp.svg'
import infj from './assets/7.svg'
import estj from './assets/3.svg'
import enfj from './assets/8.svg'


class App extends Component {
  constructor(props) {
    super(props);

    this.state = {
      counter: 0,
      questionId: 1,
      question: '',
      answerOptions: [],
      answer: '',
      answersCount: {},
      result: '',
      img : ''
    };

    this.handleAnswerSelected = this.handleAnswerSelected.bind(this);
  }

  componentDidMount() {
    const shuffledAnswerOptions = quizQuestions.map(question =>
      this.shuffleArray(question.answers)
    );
    this.setState({
      question: quizQuestions[0].question,
      answerOptions: shuffledAnswerOptions[0]
    });
  }

  shuffleArray(array) {
    var currentIndex = array.length,
      temporaryValue,
      randomIndex;

    // While there remain elements to shuffle...
    while (0 !== currentIndex) {
      // Pick a remaining element...
      randomIndex = Math.floor(Math.random() * currentIndex);
      currentIndex -= 1;

      // And swap it with the current element.
      temporaryValue = array[currentIndex];
      array[currentIndex] = array[randomIndex];
      array[randomIndex] = temporaryValue;
    }

    return array;
  }

  handleAnswerSelected(event) {
    this.setUserAnswer(event.currentTarget.value);

    if (this.state.questionId < quizQuestions.length) {
      setTimeout(() => this.setNextQuestion(), 300);
    } else {
      setTimeout(() => this.setResults(this.getResults()), 300);
    }
  }

  setUserAnswer(answer) {
    this.setState((state, props) => ({
      answersCount: {
        ...state.answersCount,
        [answer]: (state.answersCount[answer] || 0) + 1
      },
      answer: answer
    }));
  }

  

  setNextQuestion() {
    const counter = this.state.counter + 1;
    const questionId = this.state.questionId + 1;

    this.setState({
      counter: counter,
      questionId: questionId,
      question: quizQuestions[counter].question,
      answerOptions: quizQuestions[counter].answers,
      answer: ''
    });
  }

  getResults() {
    //ifser answer is 'N' or 'S'
    let Inswer  = this.state.answersCount['I'] || 0;
    let Eanswer = this.state.answersCount['E'] || 0;
    let IEanswer = Inswer - Eanswer;
    //ifser answer is 'N' or 'S'
    let Sanswer = this.state.answersCount['S'] || 0;
    let Nanswer = this.state.answersCount['N'] || 0;
    let SNanswer = Sanswer - Nanswer;
    //ifser answer is 'T' or 'F'
    let Tanswer = this.state.answersCount['T'] || 0;
    let Fanswer = this.state.answersCount['F'] || 0;
    let TFanswer = Tanswer - Fanswer;
    //ifser answer is 'J' or 'P'
    let Janswer = this.state.answersCount['J'] || 0;
    let Panswer = this.state.answersCount['P'] || 0;
    let JPanswer = Janswer - Panswer;
    //consol.log

    //return the most common answer
    let IE = IEanswer > 2 ? 'I' : 'E';
    let SN = SNanswer > 2 ? 'S' : 'N';
    let TF = TFanswer > 2 ? 'T' : 'F';
    let JP = JPanswer > 2 ? 'J' : 'P';
    // console.log(IEanswer, SNanswer, TFanswer, JPanswer);
    //return the most common answer
    let result = IE + SN + TF + JP;
    // console.log();
    // return answersCountKeys.filter(key => answersCount[key] === maxAnswerCount);

    return result;

  
  }

  setResults(result) {
    if (result === 'ESFP'){
      this.setState({result:result, img:esfp})
    }
    
    else if (result === 'ENTP'){
      this.setState({result:result, img:entp})
    }
    
    else if (result === 'ISTP'){
      this.setState({result:result, img:istp})
    }
    
    else if (result === 'ISTJ'){
      this.setState({result:result, img:istj})
    }
    
    else if (result === 'ESFJ'){
      this.setState({result:result, img:esfj})
    }
    
    else if (result === 'INTJ'){
      this.setState({result:result, img:intj})
    }
    
    else if (result === 'INFP'){
      this.setState({result:result, img:infp})
    }
    
    else if (result === 'ENFP'){
      this.setState({result:result, img:enfp})
    }
    
    else if (result === 'ESTP'){
      this.setState({result:result, img:estp})
    }
    
    else if (result === 'INTP'){
      this.setState({result:result, img:intp})
    }
    
    else if (result === 'ISFP'){
      this.setState({result:result, img:isfp})
    }
    
    else if (result === 'INFJ'){
      this.setState({result:result, img:infj})
    }

    else if (result === 'ESTJ'){
      this.setState({result:result, img:estj})
    }

    else if (result === 'ENFJ'){
      this.setState({result:result, img:enfj})
    }
    else if (result === 'ENTJ'){
      this.setState({result:result, img:entj})
    }

  }

  renderQuiz() {
    return (
      <div id = "query">
      <Quiz id
        answer={this.state.answer}
        answerOptions={this.state.answerOptions}
        questionId={this.state.questionId}
        question={this.state.question}
        questionTotal={quizQuestions.length}
        onAnswerSelected={this.handleAnswerSelected}
      />
      </div>
    );
  }

  renderResult() {
    return (
    <div className="resultss">
      {/* <Result quizResult={this.state.result} />; */}
      <img className='resultIMG' src = {this.state.img}  height='90px'/>;
    </div>
    );
  }

  render() {
    
    return (
      <div className="App" id='survey'>
        {this.state.result ? this.renderResult() : this.renderQuiz()}
      </div>
    );
  }
}   

export default App;
